.input-field {
    height: 50px;
    background: #ffffff;
    box-sizing: border-box;
    border: 1px solid #b0b0b0;
    border-radius: 1000px;
    overflow: hidden;
}

.input-field > input {
    outline: none;
    border: none;
    box-sizing: border-box;
    flex-grow: 2;
    background: #ffffff;
    font-size: 20px;
}